.news {
	padding-bottom: 180px;

	&__container {
		display: flex;
		justify-content: space-between;
	}

	&__wrapper {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
	}

	&__title {
		margin: 0;
		margin-bottom: 40px;
		font-weight: 900;
		font-size: 46px;
		line-height: 130%;
		color: var(--gray-900);
	}

	&__descr {
		margin: 0;
		margin-bottom: 60px;
		max-width: 285px;
		font-weight: 400;
		font-size: 20px;
		line-height: 150%;
		color: var(--gray-800);
	}

	&__link {
		&:hover {
			outline: 1px solid var(--primary);
		}
	}

	&__list {
		display: flex;
		gap: 30px;
	}

	&__item {
		width: 100%;
		max-width: 390px;
	}

	&-item {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		min-height: 100%;

		&__img {
			margin-bottom: 16px;
		}

		&__wrapper {
			display: flex;
			align-items: center;
			margin-bottom: 8px;
			gap: 16px;
		}

		&__info {
			font-weight: 400;
			font-size: 14px;
			line-height: 150%;
			color: var(--gray-700);
		}

		&__title {
			margin: 0;
			margin-bottom: 8px;
			font-family: var(--font-family);
			font-weight: 700;
			font-size: 20px;
			line-height: 150%;
			color: var(--gray-900);
		}

		&__descr {
			margin: 0;
			margin-bottom: 16px;
			font-weight: 400;
			font-size: 16px;
			line-height: 160%;
			color: var(--gray-800);
		}

		&__link {
			position: relative;
			font-weight: 700;
			font-size: 16px;
			line-height: 160%;
			color: var(--gray-900);

			&::after {
				content: '';
				position: absolute;
				right: -30px;
				top: 50%;
				width: 18px;
				height: 11px;
				background-image: url('../img/news-arrow.svg');
				background-repeat: no-repeat;
				transform: translateY(-50%);
				transition: right 0.3s ease-in-out;
			}

			&:hover::after {
				right: -40px;
			}
		}
	}
}
