.feedback {
	padding-bottom: 100px;
	&__title {
		margin: 0;
		margin-bottom: 60px;
		font-weight: 900;
		font-size: 46px;
		line-height: 130%;
		text-align: center;
		color: var(--gray-900);
	}

	&__card {
		margin: auto;
		border-radius: 4px;
		padding: 64px 100px;
		width: 100%;
		max-width: 1020px;
		box-shadow: 0 10px 5px -4px #e5e8ed;

		&-wrapper {
			display: grid;
			grid-template-columns: 72px auto;
			gap: 4px 20px;
		}

		&-img {
			grid-column: 1 / 2;
			grid-row: 1 / 3;
			border-radius: 100%;
			width: 72px;
			height: 72px;
			object-fit: cover;
		}

		&-name {
			align-self: flex-end;
			font-weight: 700;
			font-size: 16px;
			line-height: 160%;
			color: var(--gray-900);
		}

		&-position {
			font-weight: 400;
			font-size: 14px;
			line-height: 150%;
			color: var(--gray-700);
		}
	}

	&__text {
		position: relative;
		margin: 0;
		margin-bottom: 24px;
		font-weight: 400;
		font-size: 20px;
		line-height: 150%;
		color: var(--gray-800);

		&::before {
			content: '';
			position: absolute;
			left: -50px;
			top: 0;
			width: 28px;
			height: 20px;
			background-image: url('../img/swiper-braces.svg');
			background-size: cover;
			background-repeat: no-repeat;
		}
	}

	&__swiper {
		position: relative;

		&-btns {
			position: absolute;
			top: 50%;
			z-index: 1;
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 0 15px;
			width: 100%;
		}

		&-btn {
			display: flex;
			align-items: center;
			justify-content: center;
			background-color: var(--white);
			border-radius: 100%;
			width: 48px;
			height: 48px;
			transition: background-color 0.3s ease-in-out;

			svg {
				fill: #424551;
				transition: fill 0.3s ease-in-out;
			}

			&:hover {
				width: 48px;
				height: 48px;
				border-radius: 50%;
				background-color: var(--primary);
				svg {
					fill: var(--white);
				}
			}
		}
	}
}
