.cases {
	padding: 80px 0;
	background-color: var(--gray-200);

	&__swiper {
		margin: -20px;
		padding: 20px !important;
		margin-bottom: 60px;
	}

	&__container {
		position: relative;
	}

	&__title {
		margin: 0;
		margin-bottom: 60px;
		font-weight: 900;
		font-size: 46px;
		line-height: 130%;
		color: var(--gray-900);
	}

	&__card {
		position: relative;
		z-index: 1;
		display: flex;
		flex-direction: column;
		border-radius: 4px;
		padding: 40px;
		width: 390px;
		height: 390px;
		box-shadow: 0 0 10px 10px #e5e8ed;
		background-color: #fff;
		overflow: hidden;
		cursor: pointer;

		&::before {
			background-repeat: no-repeat;
			background-size: cover;
		}

		&::after,
		&::before {
			content: '';
			position: absolute;
			top: 0;
			right: 0;
			width: 100%;
			height: 100%;
			z-index: -1;
			opacity: 0;
			transition: opacity 0.3s ease-in-out;
		}

		&::after {
			background-color: rgba(30, 33, 44, 0.6);
		}

		&:hover::after,
		&:hover::before {
			opacity: 1;
		}

		&:hover {
			.cases__card-name {
				color: var(--white);
			}
			.cases__card-descr {
				color: var(--white);
			}
			.cases__card-stat {
				color: var(--white);
			}
		}

		&-img {
			height: 44px;
			margin: 0 0 8px 0;
			max-width: 80px;
		}

		&-name {
			position: relative;
			margin-bottom: 20px;
			padding-bottom: 20px;
			font-weight: 400;
			font-size: 14px;
			line-height: 150%;
			color: var(--gray-900);

			&::after {
				content: '';
				position: absolute;
				top: 38px;
				left: 0;
				width: 100%;
				height: 2px;
				background-image: url('../img/card-line.svg');
				background-repeat: no-repeat;
				background-size: contain;
			}
		}

		&-descr {
			margin: 0;
			margin-bottom: 20px;
			font-weight: 400;
			font-size: 16px;
			line-height: 160%;
			color: var(--gray-700);
		}

		&-list {
			display: flex;
			align-items: center;
			gap: 32px;
			margin-top: auto;
		}

		&-item {
			display: flex;
			align-items: center;
			gap: 12px;
		}

		&-stats {
			display: flex;
			flex-direction: column;
		}

		&-stat {
			&__top {
				font-family: var(--font-family);
				font-weight: 700;
				font-size: 20px;
				line-height: 150%;
				color: var(--gray-900);
			}

			&__bottom {
				font-family: var(--font-family);
				font-weight: 400;
				font-size: 12px;
				line-height: 150%;
				color: var(--gray-900);
			}
		}

		&-icon {
			width: 40px;
			height: 40px;
			background-size: cover;
			background-repeat: no-repeat;

			&-client1-1 {
				background-image: url('../img/case-client1-2.png');
			}

			&-client1-2 {
				background-image: url('../img/case-client1-3.png');
			}
			&-client2-1 {
				background-image: url('../img/case-client2-2.png');
			}
			&-client3-1 {
				background-image: url('../img/case-client3-2.png');
			}
			&-client3-2 {
				background-image: url('../img/case-client3-3.png');
			}
		}
	}

	&__swiper {
		&-slide {
			&:nth-child(1) .cases__card::before {
				background-image: url('../img/hover-card.png');
			}
			&:nth-child(2) .cases__card::before {
				background-image: url('../img/hover-card.png');
			}
			&:nth-child(3) .cases__card::before {
				background-image: url('../img/hover-card.png');
			}
			&:nth-child(4) .cases__card::before {
				background-image: url('../img/hover-card.png');
			}
			&:nth-child(5) .cases__card::before {
				background-image: url('../img/hover-card.png');
			}
			&:nth-child(6) .cases__card::before {
				background-image: url('../img/hover-card.png');
			}
		}

		&__btns {
			position: absolute;
			right: 15px;
			top: 10px;
			display: flex;
			align-items: center;
			gap: 12px;
		}

		&__btn {
			display: flex;
			align-items: center;
			justify-content: center;
			border-radius: 100%;
			width: 48px;
			height: 48px;
			transition: background-color 0.3s ease-in-out;
			svg {
				fill: #424551;
				transition: fill 0.3s ease-in-out;
			}

			&:hover {
				width: 48px;
				height: 48px;
				border-radius: 50%;
				background-color: var(--primary);
				svg {
					fill: var(--white);
				}
			}

			&-prev {
			}

			&-next {
			}
		}
	}

	&__wrapper {
		display: flex;
		align-items: center;
		justify-content: center;
		gap: 40px;

		&-text {
			font-weight: 700;
			font-size: 28px;
			line-height: 150%;
			color: var(--gray-900);
		}

		&-btn {
			font-family: var(--font-family);
			font-weight: 700;
			font-size: 16px;
			line-height: 325%;
			letter-spacing: 0.03em;
			text-align: center;
			color: var(--white);

			&:hover {
				outline: 1px solid var(--primary);
			}
		}
	}
}
