.about {
	padding: 120px 0 170px;

	&__container {
	}

	&__wrapper {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 160px;
	}

	&__descr {
		margin: 0;
		max-width: 630px;
		font-weight: 700;
		font-size: 28px;
		line-height: 150%;
		color: var(--gray-900);
	}

	&__awards {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: 40px;

		&-text {
			font-weight: 400;
			font-size: 14px;
			line-height: 150%;
			color: var(--gray-800);
		}
	}

	&__award {
		display: grid;
		align-items: center;
		grid-template-columns: 75px 145px;
		gap: 16px;
	}
}

.clients {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
