.header {
	position: absolute;
	z-index: 2;
	padding: 20px 0;
	width: 100%;
	background-color: rgba(0, 0, 0, 0);

	&__container {
		display: flex;
		align-items: center;
	}

	&__btn {
		margin-left: auto;
		font-family: var(--font-family);
		font-weight: 700;
		font-size: 16px;
		line-height: 325%;
		letter-spacing: 0.03em;
		text-align: center;
		color: var(--white);
	}
}

.nav {
	&__list {
		display: flex;
		align-items: center;
		padding-left: 60px;
		gap: 40px;
	}

	&__link {
		position: relative;
		font-family: var(--font-family);
		font-weight: 700;
		font-size: 16px;
		line-height: 160%;
		color: #9b9b9b;
		transition: color 0.3s ease-in-out;

		&:hover {
			color: var(--white);
		}

		&::before,
		&::after {
			content: '';
			position: absolute;
			right: 0;
			bottom: -4px;
			width: 100%;
			height: 2px;
			background-color: #9b9b9b;
			transform: scaleX(0);
			transition: transform 0.5s ease-in-out, background-color 0.5s ease-in-out;
		}

		&:hover::before,
		&:hover::after {
			transform: scaleX(1);
			background-color: #fff;
		}

		svg {
			fill: #9b9b9b;
			transition: fill 0.3s ease-in-out, transform 0.3s ease-in-out;
		}

		&_dropdown {
			display: flex;
			align-items: center;
			gap: 7px;
			cursor: pointer;
		}
	}
}

.logo img {
	display: block;
}

.dropdown {
	position: absolute;
	background-color: #1e212c;
	padding: 20px 40px;
	margin: 10px 0 0 -40px;
	will-change: max-height;
	opacity: 0;
	visibility: hidden;
	transform: translateY(20px);
	transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out,
		transform 0.3s ease-in-out, max-height 0.3s ease-in-out,
		will-change 0.3s ease-in-out;

	&__item {
		white-space: nowrap;
		margin: 0 0 10px 0;
	}

	&__link {
		font-weight: 700;
		font-size: 16px;
		color: #9b9b9b;
		transition: color 0.3s ease-in-out;

		&:hover {
			color: var(--white);
		}
	}

	&_visible {
		opacity: 1;
		visibility: visible;
		transform: translate(0);
	}
}

.rotate {
	fill: var(--white) !important;
	transform: rotate(180deg);
}
