.agency {
	position: relative;

	&::before {
		content: '';
		position: absolute;
		left: 100px;
		top: 25px;
		z-index: -1;
		width: 1000px;
		height: 800px;
		background-image: url('./img/agency-bg.jpg');
		background-size: 100%;
		background-repeat: no-repeat;
	}

	&__container {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&__title {
		margin: 0;
		margin-bottom: 40px;
		font-weight: 900;
		font-size: 46px;
		line-height: 130%;
		color: var(--gray-900);
	}

	&__about {
		margin-bottom: 48px;
		max-width: 390px;
		font-weight: 400;
		font-size: 16px;
		line-height: 160%;
		color: var(--gray-700);
	}

	&__btn {
		font-weight: 700;
		font-size: 14px;
		line-height: 314%;
		letter-spacing: 0.04em;
		text-align: center;
		color: var(--white);

		&:hover {
			outline: 1px solid var(--primary);
		}
	}
}
