.form-section {
	position: relative;
	z-index: 1;
	margin-bottom: 180px;
	padding: 80px 0;
	background-color: #1e212c;

	&__container {
		display: flex;
		align-items: center;
	}

	&__wrapper {
		margin: 0 0 0 auto;
		width: 50%;
	}

	&__title {
		margin: 0;
		margin-bottom: 40px;
		font-weight: 900;
		font-size: 46px;
		line-height: 130%;
		color: var(--white);
	}

	&__form {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
	}

	&__label {
		display: flex;
		flex-direction: column;
	}

	&__label:nth-child(1) {
		grid-column: 1 / 2;
		margin: 0 30px 24px 0;
	}

	&__label:nth-child(2) {
		grid-column: 2/ 3;
	}

	&__label:nth-child(3) {
		grid-column: 1/ 3;
		margin-bottom: 48px;
	}

	&__label-text {
		margin-bottom: 8px;
		font-weight: 400;
		font-size: 16px;
		line-height: 160%;
		color: var(--white);
	}

	&__input {
		border: 1px solid rgba(255, 255, 255, 0.2);
		border-radius: 4px;
		padding: 13px 16px;
		color: var(--white);
		background: rgba(255, 255, 255, 0.12);

		&::placeholder {
			font-family: var(--font-family);
			font-weight: 400;
			font-size: 16px;
			line-height: 160%;
			color: var(--white);
			opacity: 0.5;
		}
	}

	&__form-wrapper {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		grid-column: 1 / 3;
	}

	&__form-custom-checkbox {
		display: grid;
		grid-template-columns: 16px auto;
		gap: 12px;
		margin: 0 20px 0 0;

		&__text {
			font-weight: 400;
			font-size: 16px;
			line-height: 160%;
			color: var(--white);
		}
	}

	&__btn {
		font-weight: 700;
		font-size: 16px;
		letter-spacing: 0.03em;
		text-align: center;
		color: var(--white);
	}
	&::before {
		content: '';
		position: absolute;
		left: 10%;
		top: 50%;
		z-index: -1;
		width: 100%;
		height: 396px;
		max-width: 396px;
		background-image: url('../img/form-illustration-1.svg');
		background-size: contain;
		background-repeat: no-repeat;
		transform: translateY(-50%);
	}

	&::after {
		content: '';
		position: absolute;
		z-index: -1;
		background-size: contain;
		background-repeat: no-repeat;
		right: -150px;
		bottom: 40px;
		width: 300px;
		height: 350px;
		background-image: url('../img/form-illustration-2s.svg');
	}
}
