@keyframes animWrapperRight {
	from {
		transform: translateX(-100%);
		opacity: 0;
	}
	to {
		transform: translateX(0);
		opacity: 1;
	}
}

@keyframes animWrapperLeft {
	from {
		transform: translateX(200%);
		opacity: 0;
	}
	to {
		transform: translateX(0);
		opacity: 1;
	}
}

.services {
	&__title {
		margin: 0;
		margin-bottom: 40px;
		font-weight: 900;
		font-size: 46px;
		line-height: 130%;
		text-align: center;
		color: var(--gray-900);
	}

	&__tabs {
		&__nav {
			display: flex;
			align-items: center;
			justify-content: space-between;
			margin-bottom: 60px;

			&__btn {
				border-radius: 4px;
				padding: 10px 78px;
				font-weight: 700;
				font-size: 16px;
				line-height: 160%;
				text-align: center;
				color: var(--gray-600);
				transition: color 0.3s ease-in-out;

				&.active {
					border: 2px solid var(--primary);
					color: var(--primary);
				}
			}
		}

		&__wrapper {
			display: none;

			&.active {
				display: flex;
				align-items: center;
				justify-content: space-between;
			}
		}

		&__content {
			animation: animWrapperRight 0.5s ease-in-out;

			&-title {
				margin: 0;
				margin-bottom: 25px;
				font-weight: 700;
				font-size: 28px;
				line-height: 150%;
				color: var(--gray-900);
			}

			&-list {
				display: flex;
				flex-direction: column;
				justify-content: flex-start;
				margin-bottom: 60px;
				padding-left: 28px;
				gap: 12px;
			}

			&__item {
				position: relative;
				font-weight: 400;
				font-size: 16px;
				line-height: 160%;
				color: var(--gray-800);

				&::before {
					content: '';
					position: absolute;
					left: -28px;
					top: 5px;
					width: 16px;
					height: 16px;
					background-image: url('../img/tick.svg');
				}
			}

			&__btns {
				display: flex;
				gap: 24px;
			}

			&__btn {
				font-weight: 700;
				font-size: 14px;
				letter-spacing: 0.04em;
				text-align: center;
			}

			&__btn-1 {
				border: 1px solid var(--primary);
			}

			&__btn-2 {
				border: 1px solid var(--primary);

				&:hover {
					outline: 1px solid var(--white);
				}
			}
		}
	}

	&__image {
		width: 100%;
		height: 600px;
		max-width: 600px;
		object-fit: contain;
		animation: animWrapperLeft 0.5s ease-in-out;
	}
}
