.stats {
	padding: 180px 0;

	&__container {
		display: flex;
	}

	&__list {
		display: flex;
		align-items: center;
		margin: 0 auto;
		gap: 48px;
	}

	&__item {
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: 20px;
	}
}

.stat {
	width: 148px;
	height: 148px;
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;

	&-1 {
		background-image: url('../img/stat-1.svg');
	}

	&-2 {
		background-image: url('../img/stat-2.svg');
	}

	&-3 {
		background-image: url('../img/stat-3.svg');
	}

	&-4 {
		background-image: url('../img/stat-4.svg');
	}
}
