.faq {
	position: relative;
	margin-bottom: 120px;

	&::before {
		content: '';
		position: absolute;
		right: 6%;
		top: 50%;
		z-index: -1;
		width: 100%;
		height: 404px;
		max-width: 488px;
		background-image: url('../img/faq.svg');
		background-size: cover;
		background-repeat: no-repeat;
		transform: translateY(-50%);
	}

	&__container {
		display: flex;
		align-items: center;
		gap: 142px;
	}

	&__title {
		margin: 0;
		margin-bottom: 60px;
		max-width: 600px;
		font-weight: 900;
		font-size: 46px;
		line-height: 130%;
		color: var(--gray-900);
	}

	&__list {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		margin-bottom: 60px;
		padding-left: 40px;
		font-weight: 700;
		font-size: 20px;
		line-height: 150%;
		color: var(--gray-900);
		gap: 24px;
	}

	&__item {
		position: relative;
		cursor: pointer;

		&::before {
			content: '';
			position: absolute;
			left: -40px;
			top: 6px;
			background-image: url('../img/faq-open.svg');
			background-position: center;
			background-repeat: no-repeat;
			background-size: cover;
			width: 20px;
			height: 20px;
		}

		&.active::before {
			background-image: url('../img/faq-close.svg');
		}
	}

	&__descr {
		display: block; /* Чтобы элемент был всегда на странице, но невидим */
		overflow: hidden;
		max-height: 0;
		opacity: 0;
		margin: 0;
		margin: 12px 0 0;
		max-width: 556px;
		font-weight: 400;
		font-size: 16px;
		line-height: 160%;
		color: var(--gray-700);
		transform: translateY(-10px);
		transition: max-height 0.3s ease-in-out, opacity 0.3s ease-in-out,
			transform 0.3s ease-in-out;

		.active & {
			max-height: 500px; /* Достаточное значение, чтобы покрыть высоту текста */
			opacity: 1;
			transform: translateY(0);
		}
	}

	&__btn {
		display: inline-flex;
		align-items: center;
		justify-content: center;
		gap: 8px;

		&:hover {
			outline: 1px solid var(--primary);

			svg {
				fill: var(--primary);
			}
		}
	}
}

svg {
	fill: white;
}
