.hero {
	position: relative;
	padding-top: 171px;
	padding-bottom: 80px;
	height: 750px;
	background-image: url('../img/header-background.png');
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;

	&__container {
		display: flex;
		flex-direction: column;
		min-height: 100%;

		&::after {
			content: '';
			position: absolute;
			right: 10%;
			top: 20%;
			width: 500px;
			height: 500px;
			background-image: url('../img/header-illustration.svg');
			background-size: contain;
			background-repeat: no-repeat;
		}
	}

	&__title {
		margin: 0;
		margin-bottom: 60px;
		max-width: 810px;
		font-family: var(--font-family);
		font-weight: 900;
		font-size: 72px;
		line-height: 130%;
		letter-spacing: 0.01em;
		color: var(--white);

		span {
			color: var(--primary);
		}

		@media (max-width: 1500px) {
			& {
				max-width: 600px;
			}
		}
	}

	&__btn {
		display: flex;
		align-items: center;
		border: 1px solid var(--white);
		border-radius: 4px;
		padding: 0 40px;
		font-family: var(--font-family);
		font-weight: 700;
		font-size: 16px;
		line-height: 325%;
		letter-spacing: 0.03em;
		text-align: center;
		color: var(--white);
		transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

		&:hover {
			color: var(--primary);
			background-color: var(--white);
		}
	}

	&__btns {
		display: flex;
		align-items: center;
		margin-bottom: 30px;
		gap: 40px;
	}

	&__play-video {
		display: flex;
		align-items: center;
		font-weight: 700;
		font-size: 16px;
		line-height: 160%;
		text-align: center;
		color: var(--white);
		cursor: pointer;
		gap: 25px;
	}

	&__list {
		z-index: 1;
		display: flex;
		margin: auto auto 0;
		--list-gap: 180px;
		gap: var(--list-gap);
	}

	&__item {
		position: relative;
		display: flex;
		align-items: center;
		width: 100%;
		gap: 20px;

		&:not(:last-child)::after {
			content: '';
			position: absolute;
			top: 0;
			bottom: 0;
			right: calc(-1 * var(--list-gap) / 2);
			width: 1px;
			height: 100%;
			background-image: url(../img/header-divider.svg);
			background-repeat: no-repeat;
			background-size: cover;
		}

		&-text {
			font-family: var(--font-family);
			font-weight: 700;
			font-size: 20px;
			line-height: 150%;
			color: var(--white);
		}
	}
}

.play-video {
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 50%;
	width: 52px;
	height: 52px;
	background-color: var(--primary);
	transition: box-shadow 0.3s ease-in-out;

	&:hover {
		box-shadow: 0 0 0 20px rgba(119, 114, 241, 0.16);
	}
}
