@charset "UTF-8";
:root {
  --font-family: "Lato", sans-serif;
  --second-family: "Ubuntu", sans-serif;
  --content-width: 1230px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --white: #fff;
  --primary: #7772f1;
  --secondary: #00f0b5;
  --success: #03cea4;
  --danger: #ff4242;
  --warning: #f89828;
  --info: #5a87fc;
  --gray-900: #1e212c;
  --gray-800: #424551;
  --gray-700: #787a80;
  --gray-600: #9a9ca5;
  --gray-500: #b3b7bc;
  --gray-400: #d7dadd;
  --gray-300: #e5e8ed;
  --gray-200: #f4f5f6;
  --gray-100: #f9f9f9;
}

@font-face {
  font-family: "Lato";
  src: url("../fonts/Lato-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/Lato-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/Lato-Black.woff2") format("woff2");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Ubuntu";
  src: url("../fonts/Ubuntu-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 360px;
  min-height: 100%;
  font-size: 16px;
}

html,
body {
  overflow-x: hidden;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.btn {
  border-radius: 4px;
  color: var(--white);
  background-color: var(--primary);
  transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
}
.btn:hover {
  color: var(--primary);
  background-color: var(--white);
}
.btn__big {
  padding: 0 40px;
  height: 52px;
}
.btn__small {
  padding: 14px 32px;
  height: 44px;
}

.btn__transparent {
  border-radius: 4px;
  background-color: var(--white);
  transition: background-color 0.3s ease-in-out;
}
.btn__transparent:hover {
  color: var(--white);
  background-color: var(--primary);
}

.header {
  position: absolute;
  z-index: 2;
  padding: 20px 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0);
}
.header__container {
  display: flex;
  align-items: center;
}
.header__btn {
  margin-left: auto;
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 16px;
  line-height: 325%;
  letter-spacing: 0.03em;
  text-align: center;
  color: var(--white);
}

.nav__list {
  display: flex;
  align-items: center;
  padding-left: 60px;
  gap: 40px;
}
.nav__link {
  position: relative;
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 16px;
  line-height: 160%;
  color: #9b9b9b;
  transition: color 0.3s ease-in-out;
}
.nav__link:hover {
  color: var(--white);
}
.nav__link::before, .nav__link::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: -4px;
  width: 100%;
  height: 2px;
  background-color: #9b9b9b;
  transform: scaleX(0);
  transition: transform 0.5s ease-in-out, background-color 0.5s ease-in-out;
}
.nav__link:hover::before, .nav__link:hover::after {
  transform: scaleX(1);
  background-color: #fff;
}
.nav__link svg {
  fill: #9b9b9b;
  transition: fill 0.3s ease-in-out, transform 0.3s ease-in-out;
}
.nav__link_dropdown {
  display: flex;
  align-items: center;
  gap: 7px;
  cursor: pointer;
}

.logo img {
  display: block;
}

.dropdown {
  position: absolute;
  background-color: #1e212c;
  padding: 20px 40px;
  margin: 10px 0 0 -40px;
  will-change: max-height;
  opacity: 0;
  visibility: hidden;
  transform: translateY(20px);
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out, transform 0.3s ease-in-out, max-height 0.3s ease-in-out, will-change 0.3s ease-in-out;
}
.dropdown__item {
  white-space: nowrap;
  margin: 0 0 10px 0;
}
.dropdown__link {
  font-weight: 700;
  font-size: 16px;
  color: #9b9b9b;
  transition: color 0.3s ease-in-out;
}
.dropdown__link:hover {
  color: var(--white);
}
.dropdown_visible {
  opacity: 1;
  visibility: visible;
  transform: translate(0);
}

.rotate {
  fill: var(--white) !important;
  transform: rotate(180deg);
}

.hero {
  position: relative;
  padding-top: 171px;
  padding-bottom: 80px;
  height: 750px;
  background-image: url("../img/header-background.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.hero__container {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}
.hero__container::after {
  content: "";
  position: absolute;
  right: 10%;
  top: 20%;
  width: 500px;
  height: 500px;
  background-image: url("../img/header-illustration.svg");
  background-size: contain;
  background-repeat: no-repeat;
}
.hero__title {
  margin: 0;
  margin-bottom: 60px;
  max-width: 810px;
  font-family: var(--font-family);
  font-weight: 900;
  font-size: 72px;
  line-height: 130%;
  letter-spacing: 0.01em;
  color: var(--white);
}
.hero__title span {
  color: var(--primary);
}
@media (max-width: 1500px) {
  .hero__title {
    max-width: 600px;
  }
}
.hero__btn {
  display: flex;
  align-items: center;
  border: 1px solid var(--white);
  border-radius: 4px;
  padding: 0 40px;
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 16px;
  line-height: 325%;
  letter-spacing: 0.03em;
  text-align: center;
  color: var(--white);
  transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
}
.hero__btn:hover {
  color: var(--primary);
  background-color: var(--white);
}
.hero__btns {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
  gap: 40px;
}
.hero__play-video {
  display: flex;
  align-items: center;
  font-weight: 700;
  font-size: 16px;
  line-height: 160%;
  text-align: center;
  color: var(--white);
  cursor: pointer;
  gap: 25px;
}
.hero__list {
  z-index: 1;
  display: flex;
  margin: auto auto 0;
  --list-gap: 180px;
  gap: var(--list-gap);
}
.hero__item {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  gap: 20px;
}
.hero__item:not(:last-child)::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  right: calc(-1 * var(--list-gap) / 2);
  width: 1px;
  height: 100%;
  background-image: url(../img/header-divider.svg);
  background-repeat: no-repeat;
  background-size: cover;
}
.hero__item-text {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 20px;
  line-height: 150%;
  color: var(--white);
}

.play-video {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  width: 52px;
  height: 52px;
  background-color: var(--primary);
  transition: box-shadow 0.3s ease-in-out;
}
.play-video:hover {
  box-shadow: 0 0 0 20px rgba(119, 114, 241, 0.16);
}

.about {
  padding: 120px 0 170px;
}
.about__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 160px;
}
.about__descr {
  margin: 0;
  max-width: 630px;
  font-weight: 700;
  font-size: 28px;
  line-height: 150%;
  color: var(--gray-900);
}
.about__awards {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 40px;
}
.about__awards-text {
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  color: var(--gray-800);
}
.about__award {
  display: grid;
  align-items: center;
  grid-template-columns: 75px 145px;
  gap: 16px;
}

.clients {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.agency {
  position: relative;
}
.agency::before {
  content: "";
  position: absolute;
  left: 100px;
  top: 25px;
  z-index: -1;
  width: 1000px;
  height: 800px;
  background-image: url("./img/agency-bg.jpg");
  background-size: 100%;
  background-repeat: no-repeat;
}
.agency__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.agency__title {
  margin: 0;
  margin-bottom: 40px;
  font-weight: 900;
  font-size: 46px;
  line-height: 130%;
  color: var(--gray-900);
}
.agency__about {
  margin-bottom: 48px;
  max-width: 390px;
  font-weight: 400;
  font-size: 16px;
  line-height: 160%;
  color: var(--gray-700);
}
.agency__btn {
  font-weight: 700;
  font-size: 14px;
  line-height: 314%;
  letter-spacing: 0.04em;
  text-align: center;
  color: var(--white);
}
.agency__btn:hover {
  outline: 1px solid var(--primary);
}

.stats {
  padding: 180px 0;
}
.stats__container {
  display: flex;
}
.stats__list {
  display: flex;
  align-items: center;
  margin: 0 auto;
  gap: 48px;
}
.stats__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.stat {
  width: 148px;
  height: 148px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.stat-1 {
  background-image: url("../img/stat-1.svg");
}
.stat-2 {
  background-image: url("../img/stat-2.svg");
}
.stat-3 {
  background-image: url("../img/stat-3.svg");
}
.stat-4 {
  background-image: url("../img/stat-4.svg");
}

@keyframes animWrapperRight {
  from {
    transform: translateX(-100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes animWrapperLeft {
  from {
    transform: translateX(200%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}
.services__title {
  margin: 0;
  margin-bottom: 40px;
  font-weight: 900;
  font-size: 46px;
  line-height: 130%;
  text-align: center;
  color: var(--gray-900);
}
.services__tabs__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 60px;
}
.services__tabs__nav__btn {
  border-radius: 4px;
  padding: 10px 78px;
  font-weight: 700;
  font-size: 16px;
  line-height: 160%;
  text-align: center;
  color: var(--gray-600);
  transition: color 0.3s ease-in-out;
}
.services__tabs__nav__btn.active {
  border: 2px solid var(--primary);
  color: var(--primary);
}
.services__tabs__wrapper {
  display: none;
}
.services__tabs__wrapper.active {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.services__tabs__content {
  animation: animWrapperRight 0.5s ease-in-out;
}
.services__tabs__content-title {
  margin: 0;
  margin-bottom: 25px;
  font-weight: 700;
  font-size: 28px;
  line-height: 150%;
  color: var(--gray-900);
}
.services__tabs__content-list {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-bottom: 60px;
  padding-left: 28px;
  gap: 12px;
}
.services__tabs__content__item {
  position: relative;
  font-weight: 400;
  font-size: 16px;
  line-height: 160%;
  color: var(--gray-800);
}
.services__tabs__content__item::before {
  content: "";
  position: absolute;
  left: -28px;
  top: 5px;
  width: 16px;
  height: 16px;
  background-image: url("../img/tick.svg");
}
.services__tabs__content__btns {
  display: flex;
  gap: 24px;
}
.services__tabs__content__btn {
  font-weight: 700;
  font-size: 14px;
  letter-spacing: 0.04em;
  text-align: center;
}
.services__tabs__content__btn-1 {
  border: 1px solid var(--primary);
}
.services__tabs__content__btn-2 {
  border: 1px solid var(--primary);
}
.services__tabs__content__btn-2:hover {
  outline: 1px solid var(--white);
}
.services__image {
  width: 100%;
  height: 600px;
  max-width: 600px;
  object-fit: contain;
  animation: animWrapperLeft 0.5s ease-in-out;
}

.form-section {
  position: relative;
  z-index: 1;
  margin-bottom: 180px;
  padding: 80px 0;
  background-color: #1e212c;
}
.form-section__container {
  display: flex;
  align-items: center;
}
.form-section__wrapper {
  margin: 0 0 0 auto;
  width: 50%;
}
.form-section__title {
  margin: 0;
  margin-bottom: 40px;
  font-weight: 900;
  font-size: 46px;
  line-height: 130%;
  color: var(--white);
}
.form-section__form {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.form-section__label {
  display: flex;
  flex-direction: column;
}
.form-section__label:nth-child(1) {
  grid-column: 1/2;
  margin: 0 30px 24px 0;
}
.form-section__label:nth-child(2) {
  grid-column: 2/3;
}
.form-section__label:nth-child(3) {
  grid-column: 1/3;
  margin-bottom: 48px;
}
.form-section__label-text {
  margin-bottom: 8px;
  font-weight: 400;
  font-size: 16px;
  line-height: 160%;
  color: var(--white);
}
.form-section__input {
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 4px;
  padding: 13px 16px;
  color: var(--white);
  background: rgba(255, 255, 255, 0.12);
}
.form-section__input::placeholder {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 160%;
  color: var(--white);
  opacity: 0.5;
}
.form-section__form-wrapper {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  grid-column: 1/3;
}
.form-section__form-custom-checkbox {
  display: grid;
  grid-template-columns: 16px auto;
  gap: 12px;
  margin: 0 20px 0 0;
}
.form-section__form-custom-checkbox__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 160%;
  color: var(--white);
}
.form-section__btn {
  font-weight: 700;
  font-size: 16px;
  letter-spacing: 0.03em;
  text-align: center;
  color: var(--white);
}
.form-section::before {
  content: "";
  position: absolute;
  left: 10%;
  top: 50%;
  z-index: -1;
  width: 100%;
  height: 396px;
  max-width: 396px;
  background-image: url("../img/form-illustration-1.svg");
  background-size: contain;
  background-repeat: no-repeat;
  transform: translateY(-50%);
}
.form-section::after {
  content: "";
  position: absolute;
  z-index: -1;
  background-size: contain;
  background-repeat: no-repeat;
  right: -150px;
  bottom: 40px;
  width: 300px;
  height: 350px;
  background-image: url("../img/form-illustration-2s.svg");
}

.real-checkbox {
  position: absolute;
  opacity: 0;
  z-index: -1;
}
.real-checkbox:checked + .custom-checkbox__checkbox {
  background-color: #7772f1;
}
.real-checkbox:checked + .custom-checkbox__checkbox::before {
  transform: translate(-50%, -50%) scale(1);
}

.custom-checkbox:hover .custom-checkbox__checkbox {
  box-shadow: 0 0 0 6px rgba(119, 114, 241, 0.16);
}
.custom-checkbox__checkbox {
  position: relative;
  display: inline-block;
  background-color: transparent;
  border: 1px solid #7772f1;
  border-radius: 4px;
  width: 16px;
  height: 16px;
  transition: background-color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}
.custom-checkbox__checkbox::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 12px;
  height: 8px;
  transform: translate(-50%, -50%) scale(0);
  background-image: url(../img/icon__checkbox.svg);
  background-repeat: no-repeat;
  background-size: 12px 8px;
  transition: transform 0.3s ease-in-out;
}
.custom-checkbox__text {
  user-select: none;
}

.faq {
  position: relative;
  margin-bottom: 120px;
}
.faq::before {
  content: "";
  position: absolute;
  right: 6%;
  top: 50%;
  z-index: -1;
  width: 100%;
  height: 404px;
  max-width: 488px;
  background-image: url("../img/faq.svg");
  background-size: cover;
  background-repeat: no-repeat;
  transform: translateY(-50%);
}
.faq__container {
  display: flex;
  align-items: center;
  gap: 142px;
}
.faq__title {
  margin: 0;
  margin-bottom: 60px;
  max-width: 600px;
  font-weight: 900;
  font-size: 46px;
  line-height: 130%;
  color: var(--gray-900);
}
.faq__list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 60px;
  padding-left: 40px;
  font-weight: 700;
  font-size: 20px;
  line-height: 150%;
  color: var(--gray-900);
  gap: 24px;
}
.faq__item {
  position: relative;
  cursor: pointer;
}
.faq__item::before {
  content: "";
  position: absolute;
  left: -40px;
  top: 6px;
  background-image: url("../img/faq-open.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 20px;
  height: 20px;
}
.faq__item.active::before {
  background-image: url("../img/faq-close.svg");
}
.faq__descr {
  display: block; /* Чтобы элемент был всегда на странице, но невидим */
  overflow: hidden;
  max-height: 0;
  opacity: 0;
  margin: 0;
  margin: 12px 0 0;
  max-width: 556px;
  font-weight: 400;
  font-size: 16px;
  line-height: 160%;
  color: var(--gray-700);
  transform: translateY(-10px);
  transition: max-height 0.3s ease-in-out, opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}
.active .faq__descr {
  max-height: 500px; /* Достаточное значение, чтобы покрыть высоту текста */
  opacity: 1;
  transform: translateY(0);
}
.faq__btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}
.faq__btn:hover {
  outline: 1px solid var(--primary);
}
.faq__btn:hover svg {
  fill: var(--primary);
}

svg {
  fill: white;
}

.cases {
  padding: 80px 0;
  background-color: var(--gray-200);
}
.cases__swiper {
  margin: -20px;
  padding: 20px !important;
  margin-bottom: 60px;
}
.cases__container {
  position: relative;
}
.cases__title {
  margin: 0;
  margin-bottom: 60px;
  font-weight: 900;
  font-size: 46px;
  line-height: 130%;
  color: var(--gray-900);
}
.cases__card {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  border-radius: 4px;
  padding: 40px;
  width: 390px;
  height: 390px;
  box-shadow: 0 0 10px 10px #e5e8ed;
  background-color: #fff;
  overflow: hidden;
  cursor: pointer;
}
.cases__card::before {
  background-repeat: no-repeat;
  background-size: cover;
