*,
*::before,
*::after {
	box-sizing: border-box;
}

.page {
	height: 100%;
	font-family: var(--font-family, sans-serif);
	-webkit-text-size-adjust: 100%;
	scroll-behavior: smooth;
}

.page__body {
	margin: 0;
	min-width: 360px;
	min-height: 100%;
	font-size: 16px;
}

html,
body {
	overflow-x: hidden;
}

img {
	height: auto;
	max-width: 100%;
	object-fit: cover;
}

a {
	text-decoration: none;
}

.site-container {
	overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
	display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
	border: none;
	padding: 0;
	background-color: transparent;
	cursor: pointer;
}

.list-reset {
	list-style: none;
	margin: 0;
	padding: 0;
}

.input-reset {
	-webkit-appearance: none;
	appearance: none;
	border: none;
	border-radius: 0;
	background-color: #fff;

	&::-webkit-search-decoration,
	&::-webkit-search-cancel-button,
	&::-webkit-search-results-button,
	&::-webkit-search-results-decoration {
		display: none;
	}
}

.visually-hidden {
	position: absolute;
	overflow: hidden;
	margin: -1px;
	border: 0;
	padding: 0;
	width: 1px;
	height: 1px;
	clip: rect(0 0 0 0);
}

.container {
	margin: 0 auto;
	padding: 0 var(--container-offset);
	max-width: var(--container-width);
}

.centered {
	text-align: center;
}

.dis-scroll {
	position: fixed;
	left: 0;
	top: 0;
	overflow: hidden;
	width: 100%;
	height: 100vh;
	overscroll-behavior: none;
}

.page--ios .dis-scroll {
	position: relative;
}

.btn {
	border-radius: 4px;
	color: var(--white);
	background-color: var(--primary);
	transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

	&:hover {
		color: var(--primary);
		background-color: var(--white);
	}

	&__big {
		padding: 0 40px;
		height: 52px;
	}

	&__small {
		padding: 14px 32px;
		height: 44px;
	}
}

.btn__transparent {
	border-radius: 4px;
	background-color: var(--white);
	transition: background-color 0.3s ease-in-out;

	&:hover {
		color: var(--white);
		background-color: var(--primary);
	}
}
