.benefits {
	position: relative;
	padding: 100px 0 180px;

	&::before {
		content: '';
		position: absolute;
		top: 55%;
		left: 50%;
		transform: translate(-50%, -50%);
		max-width: 375px;
		max-height: 505px;
		width: 100%;
		height: 100%;
		background-image: url(../img/benefit-image-1.svg);
		background-repeat: no-repeat;
		z-index: -1;
	}

	&::after {
		content: '';
		position: absolute;
		bottom: -250px;
		left: -100px;
		width: 300px;
		height: 620px;
		background-image: url(../img/benefit-image-2.svg);
		background-size: cover;
		background-repeat: no-repeat;
		z-index: -1;
	}

	&__title {
		margin: 0;
		margin-bottom: 60px;
		font-weight: 900;
		font-size: 46px;
		line-height: 130%;
		text-align: center;
		color: var(--gray-900);
	}

	&__list {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: 24px;
	}

	&__item {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		padding: 10px;
		text-align: center;
		background-color: var(--white);
		box-shadow: inset 0 0 4px 0 #e5e8ed;
		transition: box-shadow 0.3s ease-in-out;
		width: 100%;
		height: 238px;
		max-width: 390px;

		&:nth-child(even) {
			justify-self: right;
		}

		&:hover {
			box-shadow: inset 0 0 20px 0 #e5e8ed;
		}
	}

	&__img {
		margin-bottom: 16px;
		width: 40px;
		height: 40px;
	}

	&__subtitle {
		margin-bottom: 8px;
		font-weight: 700;
		font-size: 20px;
		line-height: 150%;
		text-align: center;
		color: var(--gray-900);
	}

	&__descr {
		margin: 0;
		font-weight: 400;
		font-size: 14px;
		line-height: 150%;
		text-align: center;
		color: var(--gray-700);
	}
}
