// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
	// base
	--font-family: 'Lato', sans-serif;
	--second-family: 'Ubuntu', sans-serif;
	--content-width: 1230px;
	--container-offset: 15px;
	--container-width: calc(var(--content-width) + (var(--container-offset) * 2));

	// colors
	--white: #fff;
	--primary: #7772f1;
	--secondary: #00f0b5;
	--success: #03cea4;
	--danger: #ff4242;
	--warning: #f89828;
	--info: #5a87fc;
	--gray-900: #1e212c;
	--gray-800: #424551;
	--gray-700: #787a80;
	--gray-600: #9a9ca5;
	--gray-500: #b3b7bc;
	--gray-400: #d7dadd;
	--gray-300: #e5e8ed;
	--gray-200: #f4f5f6;
	--gray-100: #f9f9f9;
}
