.footer {
	position: relative;
	--padding-top: 80px;
	padding: var(--padding-top) 0 0 0;
	background-color: var(--gray-900);
	&__top {
		position: relative;
		display: flex;
		justify-content: space-between;
		padding-bottom: 60px;

		&::before {
			content: '';
			position: absolute;
			left: 0;
			bottom: 0;
			width: 100%;
			height: 1px;
			background-image: url('./img/footer-line.svg');
			background-repeat: no-repeat;
		}
	}

	&__wrapper {
		display: flex;
		flex-direction: column;
		margin: 0 20px 0 0;
	}

	&__logo {
		margin-bottom: 24px;
	}

	&__text {
		margin: 0;
		margin-bottom: 38px;
		max-width: 282px;
		font-weight: 400;
		font-size: 12px;
		line-height: 150%;
		color: var(--white);
	}

	&__socials {
		display: flex;
		align-items: center;
		gap: 20px;

		&-link {
			width: 24px;
			height: 24px;

			svg {
				fill: var(--white);
				transition: fill 0.3s ease-in-out;
			}

			&:hover {
				svg {
					fill: var(--primary);
				}
			}
		}
	}

	&__columns {
		display: grid;
		grid-template-columns: repeat(4, auto);
		gap: 80px;
	}

	&__column {
		display: flex;
		flex-direction: column;

		&-title {
			margin: 0;
			margin-bottom: 12px;
			font-weight: 700;
			font-size: 16px;
			line-height: 150%;
			letter-spacing: 0.06em;
			text-transform: uppercase;
			color: var(--white);
		}

		&-list {
			display: flex;
			flex-direction: column;
			gap: 8px;
		}

		&-link {
			font-weight: 400;
			font-size: 16px;
			line-height: 160%;
			color: #9b9b9b;
			transition: color 0.3s ease-in-out;

			&:hover {
				color: var(--white);
			}
		}

		&-label {
			position: relative;
			margin: 0 0 12px 0;

			svg {
				position: absolute;
				width: 12px;
				height: 8px;
				fill: #fff;
				top: 50%;
				transform: translateY(-50%);
				right: 16px;
			}
		}

		&-input {
			border: 1px solid rgba(255, 255, 255, 0.13);
			border-radius: 4px;
			background-color: rgba(255, 255, 255, 0.1);
			padding: 14px 34px 14px 16px;
			width: 100%;
			color: var(--white);
		}

		&-text {
			max-width: 286px;
			font-weight: 400;
			font-size: 10px;
			line-height: 150%;
			color: var(--white);
		}
	}

	&__awards {
		padding: 40px 0;
		&-list {
			display: flex;
			align-items: center;
			gap: 24px;
		}
	}
}
