.pricing {
	padding-bottom: 180px;

	&__title {
		margin: 0;
		margin-bottom: 60px;
		font-weight: 900;
		font-size: 46px;
		line-height: 130%;
		text-align: center;
		color: var(--gray-900);
	}

	&__list {
		display: flex;
		gap: 30px;
	}

	&-card {
		display: flex;
		flex-direction: column;
		align-items: center;
		border: 2px solid #e5e8ed;
		border-radius: 4px;
		padding: 40px 0;
		max-width: 536px;
		background-color: #fff;
		animation: animCardBottom 0.6s ease-in-out;

		&:nth-child(2) {
			border-color: #03cea4;
			background-color: #1e212c;
			box-shadow: 0 0 30px 0 #1e212c;
			animation: animCardTop 0.6s ease-in-out;

			& .pricing-card__title {
				color: var(--white);
			}

			& .pricing-card__price {
				color: var(--success);

				&-small {
					color: var(--success);
				}
			}

			& .pricing-card__link {
				color: var(--white);
				background-color: var(--primary);

				&:hover {
					color: var(--primary);
					background-color: var(--white);
				}
			}

			& .pricing-card__item {
				color: var(--white);
				background-image: url('../img/card-mark-green.svg');

				&.unavailable {
					background-image: url('../img/card-cross.svg');
				}
			}
		}

		&__title {
			margin: 0;
			margin-bottom: 8px;
			font-weight: 700;
			font-size: 28px;
			line-height: 150%;
			text-align: center;
			color: var(--gray-900);
		}

		&__price {
			display: flex;
			margin-bottom: 16px;
			font-weight: 900;
			font-size: 46px;
			line-height: 130%;
			text-align: center;
			color: var(--primary);

			&-small {
				font-weight: 700;
				font-size: 20px;
				line-height: 150%;
				color: var(--primary);
			}
		}

		&__img {
			display: block;
			margin-bottom: 24px;
		}

		&__list {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			margin-bottom: 32px;
			gap: 8px;
		}

		&__item {
			font-weight: 400;
			font-size: 16px;
			line-height: 160%;
			text-align: center;
			color: var(--gray-900);
			background-image: url('../img/card-mark.svg');
			background-repeat: no-repeat;
			background-position: left center;
			background-size: 14px 10px;
			padding: 0 0 0 28px;

			&.unavailable {
				font-family: var(--font-family);
				font-weight: 400;
				font-size: 16px;
				line-height: 160%;
				text-align: center;
				color: var(--gray-600);
				background-image: url('../img/card-cross.svg');
				background-size: 11px 11px;
			}
		}

		&__link {
			border: 1px solid var(--primary);
			border-radius: 4px;
			padding: 14px 40px;
			font-weight: 700;
			font-size: 14px;
			letter-spacing: 0.04em;
			text-align: center;
			color: var(--primary);
			transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

			&:hover {
				color: var(--white);
				background-color: var(--primary);
			}
		}
	}
}

.tabs {
	display: flex;
	flex-direction: column;
	align-items: center;

	&__item {
		display: none;

		&.active {
			display: block;
		}
	}
	&__nav {
		overflow: hidden;
		margin: 0 0 60px 0;
		border: 1px solid #7772f1;
		border-radius: 100px;
		&-btn {
			border-radius: 100px;
			background-color: rgba(0, 0, 0, 0);
			padding: 10px;
			width: 150px;
			font-size: 16px;
			font-weight: 700;
			color: #7772f1;
			border-top-right-radius: 0px;
			border-bottom-right-radius: 0px;
			transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out,
				width 0.3s ease-in-out, border-radius 0.3s ease-in-out;
			&.active {
				background-color: #7772f1;
				color: #fff;
				width: 210px;
				border-top-right-radius: 100px;
				border-bottom-right-radius: 100px;
			}
		}
	}

	&__content {
		max-width: 100%;
	}
}
